.wrapper{
    display: flex;
    justify-content: center;
    .inner_wrapper {
        width: var(--default_page_width);
        padding-bottom: 200px;
        .header{
            display: flex;
            justify-content: space-between;
            align-items: center;
            .main_header {
                margin: 48px 0 15px;
                padding-left: 10px;
                border-left: 4px solid black;
                font-size: 40px;
                font-weight: bolder;
            }
            .sub_header {
                margin-bottom: 70px;
                font-size: 16px;
            }
            .createProduct {
                margin-top: 24px;
                width: 300px;
                height: 60px;
                font-size: 17px;
            }
        }
        @media (max-width: 700px) {
            .header{
                display: block;
            }
        }
        .content {
            margin-top: 70px;
            .order_history {
                .order_history_header{
                    padding-bottom: 50px;
                    font-size: 30px;
                    font-weight: bolder;
                    text-align: center;
                }
                .order_history_content{
                    color: #161d25;
                    font-size: 14px;
                }
                .order_history_list {
                    margin-top: 20px;
                    display: grid;
                    grid-template-columns: repeat(5, minmax(0, 1fr));
                    row-gap: 40px;
                    column-gap: 10px;
                }
                @media (max-width: 1000px) {
                    .order_history_list{
                        grid-template-columns: repeat(4, minmax(0, 1fr));
                    }
                }
                @media (max-width: 780px) {
                    .order_history_list{
                        grid-template-columns: repeat(3, minmax(0, 1fr));
                    }
                }
                @media (max-width: 550px) {
                    .order_history_list{
                        grid-template-columns: repeat(2, minmax(0, 1fr));
                    }
                }
                @media (max-width: 400px) {
                    .order_history_list{
                        grid-template-columns: repeat(1, minmax(0, 1fr));
                    }
                }
            }
        }
        .footer {
            margin-top: 80px;
            display: flex;
            justify-content: center;
            .pages{
                display: flex;
                .paging {
                    cursor: pointer;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 38px;
                    height: 38px;
                    &:hover {
                        background-color: rgb(13,60,85);
                        color: #fff;
                    }
                    & +.paging {
                        margin-left: 2px;
                    }
                }
                .paging_active {
                    background-color: rgb(13,60,85);
                    color: #fff;
                }
                .leftArrow, .rightArrow {
                    cursor: pointer;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 38px;
                    height: 38px;
                    &:hover {
                        background-color: rgb(13,60,85);
                        .rightArrow_icon, .leftArrow_icon {
                            fill: #fff;
                        }
                    }
                }
                .rightArrow_icon, .leftArrow_icon {
                    width: 10px;
                    height: 10px;
                }
            }
        }
        @media (max-width: 940px) {
            .content{
                display: block;
                .account_details {
                    margin-top: 70px;
                }
            }
        }
    }   
}     