        display: block;
    }
    .fullScreen .picture_wrapper {
        height: auto;
    }
  }

@keyframes shaking {
    0%{
        transform: translate(0, 0);
    }
    1.5%{
        transform: translate(10%, 0);
    }
    3%{
        transform: translate(0, 0);
    }
    4.5%{
        transform: translate(-10%, 0);
    }
    6%{
        transform: translate(0, 0);
    }
    7.5%{
        transform: translate(10%, 0);
    }
    9%{
        transform: translate(0, 0);
    }
    100%{
        transform: translate(0, 0);
    }
}