.inspires {
    position: relative;
    margin-bottom: 100px;
    .inspires_grid {
        display: grid;
        width: 100%;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        .inspires_content {
            margin: 15px;
            position: relative;
            overflow: hidden;
            .shadow_background {
                z-index: 1;
                position: absolute;
                top: 0;
                width: 100%;
                height: 100%;
            }

            .picture {
                height: 100%;
                width: 100%;
                display: block;
                transition: transform 3s;
            }
            
           
            .content_wrapper {
                position: absolute;
                width: 50%;
                top: 50%;
                transform: translate(-100%, -50%);
                opacity: 0;
                transition: all 0.5s ease-in-out;
                .content_sub{
                    margin-bottom: 10px;
                    color: rgb(255,255,255);
                    font-size: 25px;
                }
                .content_header {
                    color: rgb(255,255,255);
                    font-size: 50px;
                    font-weight: bolder;
                }
                @media (max-width: 460px) {
                    .content_sub{
                        font-size: 15px;
                    }
                    .content_header {
                        font-size: 30px;
                    }
            
                }
                @media (max-width: 285px) {
                    .content_sub{
                        font-size: 8px;
                    }
                    .content_header {
                        font-size: 20px;
                    }
            
                }
                @media (max-width: 190px) {
                    .content_sub{
                        font-size: 5px;
                    }
                    .content_header {
                        font-size: 10px;
                    }
            
                }
            }

            .shadow_background:hover + .picture{
                transform: scale(1.2);
            }
            .shadow_background:hover ~ .content_wrapper {
                opacity: 1;
                transform: translate(15%, -50%);
            }
        }
    }
    @media (max-width: 990px) {
        .inspires_grid {
            grid-template-columns: repeat(1, minmax(0, 1fr));
        }

    }
    .inspires_message {
        position: absolute;
        top: 0;
        background-color: rgb(255,255,255);
        width: 300px;
        height: 300px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        align-items: center;
        padding: 0 40px;
        .inspires_message_text {
            font-size: 50px;
            font-weight: bolder;
        }
    }
    @media (max-width: 770px) {
        .inspires_message {
            display: none;
        }

    }
}